.vue-wp-notice {
  background: #fff;
  border-left: 4px solid #fff;
  -webkit-box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.1);
          box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.1);
  margin: 5px 15px 2px 0;
  padding: 1px 12px;
}
.vue-wp-notice p {
    margin: 0.5em 0;
    padding: 2px;
}
.vue-wp-notice--large {
    padding: 10px 20px;
}
.vue-wp-notice.notice-success {
    border-left-color: #46b450;
}
.vue-wp-notice.notice-warning {
    border-left-color: #ffb900;
}
.vue-wp-notice.notice-error {
    border-left-color: #dc3232;
}
.vue-wp-notice.notice-error a {
      text-decoration: underline;
}
.vue-wp-notice.notice-info {
    border-left-color: #00a0d2;
}
.vue-wp-notice .notice-dismiss {
    position: absolute;
    top: 0;
    right: 1px;
    border: none;
    margin: 0;
    padding: 9px;
    background: none;
    color: #72777c;
    cursor: pointer;
}
.vue-wp-notice .notice-dismiss:before {
      background: 0 0;
      color: #72777c;
      content: "\F153";
      display: block;
      font: 400 16px/20px dashicons;
      speak: none;
      height: 20px;
      text-align: center;
      width: 20px;
      -webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale;
}
.vue-wp-notice .notice-dismiss:focus {
      outline: none;
      -webkit-box-shadow: 0 0 0 1px #5b9dd9, 0 0 2px 1px rgba(30, 140, 190, 0.8);
              box-shadow: 0 0 0 1px #5b9dd9, 0 0 2px 1px rgba(30, 140, 190, 0.8);
}
.vue-wp-notice .notice-dismiss:hover:before, .vue-wp-notice .notice-dismiss:active:before, .vue-wp-notice .notice-dismiss:focus:before {
      color: #c00;
}
.vue-wp-notice.is-dismissible {
    padding-right: 38px;
    position: relative;
}
.vue-wp-notice.is-alt {
    -webkit-box-shadow: none;
            box-shadow: none;
}
.vue-wp-notice.is-alt.notice-success {
      background-color: #ecf7ed;
}
.vue-wp-notice.is-alt.notice-warning {
      background-color: #fff8e5;
}
.vue-wp-notice.is-alt.notice-error {
      background-color: #fbeaea;
}
.vue-wp-notice.is-alt.notice-info {
      background-color: #e5f5fa;
}
